import { connect } from "react-redux";
import { logoutThunk } from "../../../../redux/authReducer";
import { searchUsersThunk } from "../../../../redux/searchReducer";
import Header from "./Header";

let mapStateToProps = (state) => {
  return {
    auth: state.auth,
    avatar: state.auth.myProfile.avatar.small,
    inputSearchResult: state.searchPage.inputSearchResult,
    inputIsFetching: state.searchPage.inputIsFetching,
  };
};

const HeaderContainer = connect(mapStateToProps, { logoutThunk, searchUsersThunk })(Header);

export default HeaderContainer;
