import React, { memo } from "react";
import s from "./Header.module.css";
import Searchsvg from "../../../../imgs/Search.svg";
import { Link, useNavigate } from "react-router-dom";
import Dialogsvg from "../../../../imgs/Dialog.svg";
import Notificationsvg from "../../../../imgs/Bell.svg";
import HeaderLogged from "./HeaderLogged";
import { Autocomplete, Avatar, Box, CircularProgress, TextField } from "@mui/material";
import { useState } from "react";

const Header = memo((props) => {
  const [searchbarText, setSearchbarText] = useState("");

  const navigate = useNavigate();

  function searchNewUsers(value) {
    setSearchbarText(value);
    props.searchUsersThunk(value);
  }

  function clearSearchBarText() {
    setSearchbarText("");
    props.searchUsersThunk("");
  }
  return (
    <header className={s.header}>
      <div className={s.searchbar}>
        <Autocomplete
          sx={{ width: 300 }}
          options={props.inputSearchResult}
          getOptionLabel={(option) => option.name}
          loading={props.inputIsFetching}
          inputValue={searchbarText}
          isOptionEqualToValue={(option, value) => {
            return option.name === value.name;
          }}
          onChange={(event, newValue) => {
            if (newValue) {
              navigate(`/profile/${newValue.id}`);
            }
          }}
          onInputChange={(event, newValue) => {
            searchNewUsers(newValue);
          }}
          onOpen={clearSearchBarText}
          onClose={clearSearchBarText}
          renderOption={(props, option) => (
            <Box component="li" sx={{ "& > img": { mr: 2, flexShrink: 0 } }} {...props}>
              <Avatar src={option.avatar} alt="" />
              {option.name}
            </Box>
          )}
          renderInput={(params) => (
            <TextField
              {...params}
              placeholder="Search..."
              InputProps={{
                ...params.InputProps,
                endAdornment: (
                  <React.Fragment>
                    {props.inputIsFetching ? <CircularProgress color="inherit" size={20} /> : null}
                    {params.InputProps.endAdornment}
                  </React.Fragment>
                ),
              }}
            />
          )}
        />
        <img className={s.searchbar_Img} src={Searchsvg} alt="" />
      </div>
      <div className={s.navigation}>
        <Link className={s.iconLink} to="/dialog">
          <img src={Dialogsvg} alt="" />
        </Link>
        <Link className={s.iconLink} to="/notification">
          <img src={Notificationsvg} alt="" />
        </Link>
        <div className={s.profilePreview}>
          <HeaderLogged
            isAuth={props.auth.isAuth}
            avatar={props.avatar}
            id={props.auth.id}
            name={props.auth.myProfile.fullName}
            logout={props.logoutThunk}
          />
        </div>
      </div>
    </header>
  );
});

export default Header;
