import { useFriendAdd, useFriendRemove, useGetUsers, useSearchUsers } from "../customHooks/fetchFromAPI";
import { createSlice } from "@reduxjs/toolkit";

let initialState = {
  users: [],
  isNoneUsers: null,
  count: 7,
  currentPage: 1,
  totalItems: 1,
  isFetching: false,
  buttonIsFetching: [],
  inputSearchResult: [],
  inputCount: 10,
  inputIsFetching: false,
};

const searchReducer = createSlice({
  name: "searchReducer",

  initialState: initialState,

  reducers: {
    addFriend(state, action) {
      state.users = state.users.map((user) => {
        if (user.id === action.payload) {
          return { ...user, followed: true };
        }
        return user;
      });
    },
    removeFriend(state, action) {
      state.users = state.users.map((user) => {
        if (user.id === action.payload) {
          return { ...user, followed: false };
        }
        return user;
      });
    },
    setUsers(state, action) {
      state.users = action.payload;
    },
    setTotalItems(state, action) {
      state.totalItems = action.payload;
    },
    setCurrentPage(state, action) {
      state.currentPage = action.payload;
    },
    toggleSeachIsFetching(state, action) {
      state.isFetching = action.payload;
    },
    setButtonIsFetching(state, action) {
      state.buttonIsFetching = action.payload.fetching
        ? [...state.buttonIsFetching, action.payload.userID]
        : state.buttonIsFetching.filter((id) => id !== action.payload.userID);
    },
    setIsNoneUsers(state, action) {
      state.isNoneUsers = action.payload;
    },
    setInputSearchResult(state, action) {
      let mapedResults = action.payload.map((result) => {
        return {
          id: result.id,
          name: result.name,
          avatar: result.photos.small ? result.photos.small : "https://cdn-icons-png.flaticon.com/512/21/21104.png",
        };
      });
      state.inputSearchResult = mapedResults;
    },
    setInputIsFetching(state, action) {
      state.inputIsFetching = action.payload;
    },
  },
});

export default searchReducer.reducer;
export const {
  addFriend,
  removeFriend,
  setUsers,
  setTotalItems,
  toggleSeachIsFetching,
  setButtonIsFetching,
  setIsNoneUsers,
  setCurrentPage,
  setInputSearchResult,
  setInputIsFetching,
} = searchReducer.actions;

export const getUsersThunk = (navigate, id, currentPage, onlyFriends) => {
  return async (dispatch, getState) => {
    dispatch(toggleSeachIsFetching(true));
    //Check for valid id
    if (currentPage !== id) {
      dispatch(setCurrentPage(Number(id)));
    }
    //Fetch users
    try {
      const data = await useGetUsers(getState().searchPage.count, id, onlyFriends);
      let totalPages = Math.ceil(data.totalCount / getState().searchPage.count);
      console.log(data);
      if (totalPages === 0) {
        dispatch(setIsNoneUsers(true));
        dispatch(toggleSeachIsFetching(false));
      } else {
        dispatch(setIsNoneUsers(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalItems(data.totalCount));
        dispatch(toggleSeachIsFetching(false));
        console.log(id, totalPages);

        //Check if id bigger than totalPages
        if (id > totalPages) {
          navigate("/search/1");
        }
      }
    } catch (err) {
      console.error(err);
      navigate("/search/1");
    }
  };
};

export const searchUsersThunk = (term) => {
  return async (dispatch, getState) => {
    dispatch(setInputIsFetching(true));
    const data = await useSearchUsers(getState().searchPage.inputCount, term);
    dispatch(setInputSearchResult(data.items));
    dispatch(setInputIsFetching(false));
  };
};

export const removeFriendThunk = (userID) => {
  return async (dispatch) => {
    dispatch(setButtonIsFetching(true, userID));
    const id = await useFriendRemove(userID);
    dispatch(removeFriend(id));
    dispatch(setButtonIsFetching(false, userID));
  };
};

export const addFriendThunk = (userID) => {
  return async (dispatch) => {
    dispatch(setButtonIsFetching(true, userID));
    const id = await useFriendAdd(userID);
    dispatch(addFriend(id));
    dispatch(setButtonIsFetching(false, userID));
  };
};
